import React from "react";
import cross from "../../assets/img/cross.png";
import oval from "../../assets/img/o.png";

const Info = ({ turn, pressedRestart }) => {
  const IconTypes = () => (
    <div className="game-info-icons">
      <img src={cross} />
      <img src={oval} />
    </div>
  );
  const Turn = () => (
    <div className="game-info-turn">
      <img src={turn === "cross" ? cross : oval} />
      <p>TURN</p>
    </div>
  );
  const RestartButton = () => (
    <div className="game-info-restart">
      <button className="btn-grey" onClick={() => pressedRestart()}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          height="24"
          viewBox="0 0 24 24"
          width="24"
        >
          <path d="M0 0h24v24H0z" fill="none" />
          <path d="M17.65 6.35C16.2 4.9 14.21 4 12 4c-4.42 0-7.99 3.58-7.99 8s3.57 8 7.99 8c3.73 0 6.84-2.55 7.73-6h-2.08c-.82 2.33-3.04 4-5.65 4-3.31 0-6-2.69-6-6s2.69-6 6-6c1.66 0 3.14.69 4.22 1.78L13 11h7V4l-2.35 2.35z" />
        </svg>
      </button>
    </div>
  );

  return (
    <div id="game-info">
      <IconTypes />
      <Turn />
      <RestartButton />
    </div>
  );
};

export default Info;
